import React from 'react';
import ArticlesList from '../components/ArticleList';
import articles from './article-content';

const ArticleListPage = () => (
    <div>
        <h1>Articles </h1>
        <ArticlesList articles={articles}/>
       
    </div>
    
);

export default ArticleListPage;


